import React, { useState } from 'react';
import { Button, Col, Form, FormGroup, Input, Label, Row, Spinner, Table } from 'reactstrap';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import MaterialIcon from '@material/react-material-icon';
import { useQuery } from '@tanstack/react-query';
import getBase64 from '../../../../utils/generateFile';
import useAxios from '../../../../hooks/useAxios';

const AktaPendirian = ({ setSuccessMsg, setErrMsg }) => {
  const [datas, setDatas] = useState();
  const [file, setFile] = useState();
  const [isSending, setIsSending] = useState(false);
  const [selectedId, setSelectedId] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [isOpenForm, setIsOpenForm] = useState(false);

  const api = useAxios();

  const { isLoading, error, data, refetch } = useQuery({
    queryKey: ['akta'],
    queryFn: () =>
      api.get(`vapi/akta/view`).then((res) => {
        return res.data.data;
      }),
  });

  const handleDoc = (e) => {
    getBase64(e.target.files[0])
      .then((res) => setFile(res.split(',')[1]))
      .catch((err) => console.log(err));
  };

  const handleChange = (e) => {
    setDatas((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);
    datas.file = file;
    datas.jenis = 'Pendirian';
    await api
      .post('vapi/akta/store', datas)
      .then(() => {
        refetch();
        setSuccessMsg('Data Akta Pendirian Perusahaan berhasil disimpan.');
      })
      .catch(() => {
        setErrMsg('Gagal menyimpan data. Silakan coba lagi.');
      });
    setIsSending(false);
  };

  const handleDelete = async (id) => {
    setSelectedId(id);
    setIsDeleting(true);
    await api
      .get(`vapi/akta/delete/${id}`)
      .then(() => {
        refetch();
      })
      .catch((err) => console.log(err));
    setIsDeleting(false);
  };

  return (
    <>
      {isLoading ? (
        'Loading..'
      ) : error ? (
        'Something went wrong.'
      ) : data.length > 0 ? (
        <>
          <h5>Akta Pendirian Perusahaan</h5>
          <Table hover responsive>
            <tbody>
              {data.map((ak) => (
                <tr key={ak.id_akta}>
                  <td>{ak.no_akta}</td>
                  <td>{ak.tgl_terbit}</td>
                  <td>{ak.nama_notaris}</td>
                  <td>{ak.jenis}</td>
                  <td>
                    <abbr title="Download file">
                      <Link to="">
                        <MaterialIcon icon="download" />
                      </Link>
                    </abbr>
                  </td>
                  <td style={{ textAlign: 'right', width: '100px' }}>
                    {isDeleting && selectedId === ak.id_akta ? (
                      <span className="text-danger">Deleting..</span>
                    ) : (
                      <MaterialIcon
                        icon="delete"
                        style={{ fontSize: '16px', cursor: 'pointer', color: 'red' }}
                        onClick={() => handleDelete(ak.id_akta)}
                      />
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
          <div className="d-flex justify-content-end mt-2">
            {isOpenForm ? (
              <Button
                type="button"
                color="dark"
                size="sm"
                outline
                onClick={() => setIsOpenForm(false)}
                content="center"
              >
                Tutup
              </Button>
            ) : (
              <Button
                className="mb-4"
                type="button"
                color="dark"
                size="sm"
                outline
                onClick={() => setIsOpenForm(true)}
              >
                Tambah Akta
              </Button>
            )}
          </div>
        </>
      ) : (
        setIsOpenForm(true)
      )}
      {isOpenForm && (
        <Form onSubmit={handleSubmit}>
          <Col className="bg-light-primary rounded-4 px-3 pt-3 pb-1 mt-3 mb-3">
            <Row>
              <Col sm="12" lg="4">
                <FormGroup>
                  <Label htmlFor="no_akta">Akta Pendirian Perusahaan</Label>
                  <Input
                    type="text"
                    id="no_akta"
                    name="no_akta"
                    placeholder="Nomor"
                    onChange={(e) => handleChange(e)}
                  />
                </FormGroup>
              </Col>
              <Col sm="12" lg="4">
                <FormGroup>
                  <Label htmlFor="tgl_terbit">&nbsp;</Label>
                  <Input
                    type="text"
                    id="tgl_terbit"
                    name="tgl_terbit"
                    placeholder="Tanggal dikeluarkan"
                    onChange={(e) => handleChange(e)}
                  />
                </FormGroup>
              </Col>
              <Col sm="12" lg="4">
                <FormGroup>
                  <Label htmlFor="nama_notaris">&nbsp;</Label>
                  <Input
                    type="text"
                    id="nama_notaris"
                    name="nama_notaris"
                    placeholder="Notaris"
                    onChange={(e) => handleChange(e)}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row className="mb-4">
              <Col sm="12" lg="4">
                <FormGroup className="position-relative">
                  <Input
                    type="file"
                    id="fileAktaPendirian"
                    name="fileAktaPendirian"
                    onChange={(e) => handleDoc(e)}
                  />
                  <small className="text-muted position-absolute">Upload file (max. 10mb)</small>
                </FormGroup>
              </Col>
              <Col sm="12" lg="4">
                <Button type="submit" color="secondary">
                  {isSending ? (
                    <div className="d-flex align-items-center gap-2">
                      <Spinner size="sm" />
                      Menyimpan..
                    </div>
                  ) : (
                    'Simpan'
                  )}
                </Button>
              </Col>
            </Row>
          </Col>
        </Form>
      )}
    </>
  );
};

AktaPendirian.propTypes = {
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default AktaPendirian;
