import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Col, Form, FormGroup, Input, Label, Row, Spinner } from 'reactstrap';
import { Link } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import getBase64 from '../../../../utils/generateFile';
import useAxios from '../../../../hooks/useAxios';
import FileView from '../../../../components/fileview/FileView';

const uploads = [
  { id: 1, label: 'Company Profil', name: 'profil' },
  {
    id: 2,
    label: 'KTP (Pengurus Yang Tertera Pada Akta Perubahan Terakhir Perusahaan)',
    name: 'ktp',
  },
  {
    id: 3,
    label: 'SK Kemenkum HAM',
    name: 'sk_kemenkumham',
  },
  {
    id: 4,
    label: 'Pakta Integritas',
    name: 'fakta_integritas',
    template: true,
  },
  {
    id: 5,
    label: 'Surat Pemberitahuan Tahunan (SPT) 3 Tahun Terakhir',
    name: 'spt',
  },
  {
    id: 6,
    label: 'Laporan Pajak PPh 3 Bulan Terakhir',
    name: 'pph',
  },
  {
    id: 7,
    label: 'Laporan Keuangan 3 Tahun Terakhir (Perusahaan Yang Telah Diaudit Menjadi Nilai Tambah)',
    name: 'lap_keuangan',
  },
  {
    id: 8,
    label: 'Rekening Koran 3 Bulan Terakhir',
    name: 'rek_koran',
  },
];

const Lainnya = ({ setSuccessMsg, setErrMsg }) => {
  const [selectedId, setSelectedId] = useState();
  const [selectedName, setSelectedName] = useState();
  const [file, setFile] = useState();
  const [isSending, setIsSending] = useState(false);

  const api = useAxios();

  const { isLoading, error, data } = useQuery({
    queryKey: ['documents'],
    queryFn: () =>
      api.get(`vapi/file/dokumen-perusahaan`).then((res) => {
        // if (res.data.data?.email_alternatif !== null) {
        //   setGeneralIsDone(true);
        // }
        return res.data.data;
      }),
  });

  const handleChange = (id, val, name) => {
    setSelectedId(id);
    setSelectedName(name);
    getBase64(val)
      .then((res) => setFile(res.split(',')[1]))
      .catch((err) => console.log(err));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);

    const datas = {
      whatfile: selectedName,
      file,
    };

    await api.post('vapi/file/upload', datas).then((res) => {
      console.log(res);
      if (res.data.success === true) {
        setSuccessMsg(res.data.message);
      } else {
        setErrMsg('Upload file gagal. Silakan coba lagi.');
      }
    });

    setIsSending(false);
  };

  return isLoading
    ? 'Loading...'
    : error
    ? 'Something went wrong.'
    : uploads.map((item) =>
        data.map(
          (doc) =>
            doc.id === item.name && (
              <Row key={item.id} className="mb-4">
                <Form onSubmit={handleSubmit}>
                  <FormGroup>
                    <Row>
                      <Label htmlFor={item.name}>{item.label}</Label>
                      {doc.file_name !== null ? (
                        <FileView title="" filename={doc.file_name} />
                      ) : (
                        <>
                          <Col sm="10">
                            <Input
                              type="file"
                              id={item.name}
                              name={item.name}
                              onChange={(e) => handleChange(item.id, e.target.files[0], item.name)}
                              accept="application/pdf"
                              required
                            />
                            {item?.template ? (
                              <div className="d-flex justify-content-between">
                                <small className="text-muted">Upload file (max. 10mb)</small>
                                <i>
                                  <Link to="/" className="text-decoration-none">
                                    *Download template
                                  </Link>
                                </i>
                              </div>
                            ) : (
                              <small className="text-muted position-absolute">
                                Upload file (max. 10mb)
                              </small>
                            )}
                          </Col>
                          <Col sm="2">
                            <Button
                              type="submit"
                              block
                              disabled={
                                item.id !== selectedId || (item.id === selectedId && isSending)
                              }
                            >
                              {isSending && item.id === selectedId ? (
                                <div className="d-flex align-items-center justify-content-center gap-2">
                                  <Spinner size="sm" />
                                  Uploading..
                                </div>
                              ) : (
                                'Upload'
                              )}
                            </Button>
                          </Col>
                        </>
                      )}
                    </Row>
                  </FormGroup>
                </Form>
              </Row>
            ),
        ),
      );
};

Lainnya.propTypes = {
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default Lainnya;
